* {
  box-sizing: border-box;
  word-break: keep-all;
  overflow-wrap: break-word;
  font: inherit;
  position: relative;
  background: transparent;
  border: none;
}
p {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
  background: transparent;
  font: inherit;
}

.Main_background {
  position: relative;

  color: white;
  display: flex;

  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.Home_banner {
  width: 100%;
  height: 440px;
}

.slide-img {
  width: 100%;
  height: 440px;

  position: relative;
}

.Banner-container {
  position: absolute;
  background: #222222;
  top: 0;
  left: 0;
  width: 25%;
  height: 102%;
  opacity: 60%;

  display: flex;
  flex-direction: column;

  justify-content: center;
  cursor: pointer;
  padding-right: 38px;
}

.Banner-text1 {
  width: 100%;

  color: #feb47b;
  font-size: 40px;

  font-weight: 700;
  line-height: 47.73px;
  word-break: keep-all;
  overflow-wrap: normal;
  text-align: right;
}
.Banner-text2 {
  text-align: right;
  color: #fff;

  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
/*backdrop-filter: blur(12px);*/

.slide_copy {
  width: 100%;

  margin-top: 40px;
}

.Slider_label {
  font-size: 18px;
  font-weight: 700;
  line-height: 21px;
  letter-spacing: 0em;
  text-align: left;
  color: #ffffff;
  border: 0;
  padding-left: 40px;
  margin-bottom: 8px;
}

.Slider_more {
  font-size: 14px;
  font-weight: 400;
  line-height: 17px;
  letter-spacing: 0em;
  text-align: left;
  color: #feb47b;
  margin-left: 12px;
}

.copy_img_120 {
  height: 120px;
  width: 95%;

  border-radius: 8px;
  object-fit: cover;
}
.copy_img_250 {
  width: 100%;
  border-radius: 8px;
  height: 250px;
  object-fit: cover;
}

.copy_img_400 {
  height: 400px;
  width: 95%;

  border-radius: 8px;
  object-fit: cover;
}
.slide_copy-imgs {
  margin: 0px 20px 0px 40px;
}

.weekly_top10_all {
  margin: 0px 20px 0px 40px;
  display: flex;
  flex-direction: row;
}

.weekly {
  display: flex;
}
.weekly_index {
  color: #feb47b;
  font-family: Pretendard;
  font-size: 128px;
  font-style: normal;
  font-weight: 800;
  line-height: 70%;
}
.weekly_TOP10 {
  display: flex;
  position: relative;
  gap: 0px;
  align-items: flex-end;
}
