@media (min-width: 681px) {
  /*appbar*/
  .black-nav {
    white-space: normal;
    margin-bottom: 46px;
  }

  .nav-content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100vw;
    background-color: #222;
    padding: 10px 60px 10px 30px;
    position: relative;
    height: 46px;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
  }

  .nav-text {
    color: #feb47b;
    text-align: center;
    font-family: Pretendard;
    font-size: 15px;
    font-style: normal;
    font-weight: 300;
    line-height: normal;
    text-decoration: none;
  }
  .nav-search {
    display: flex;
    width: 160px;
    padding: 8px 12px;
    align-items: center;
    gap: 2px;
    border-radius: 6px;
    background: #3c3c3c;
  }
  /*appbar*/
}
