.Modal_overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  overflow-y: scroll;
  z-index: 10000;
}

.Modal_position {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  margin: calc(70px + 3vh) 0;
  -webkit-overflow-scrolling: touch;
  overflow: auto;
  outline: none;
}
.Work_modal-content {
  border-radius: 8px;
  background-color: #323232;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 440px;
  height: auto;
  flex-shrink: 0;
  min-height: 1024px;
}

.Work_modal-top-con {
  width: 100%;
  border-radius: 6px;
  overflow: hidden;
  background-color: #323232;
}
.Work_description {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.Work_description-top {
  width: 100%;
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.Work_title {
  display: flex;
  align-items: center;
  justify-content: space-between;

  color: #fff;
  font-family: Pretendard;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}
.Work_hashtags {
  width: 100%;
  font-size: 1rem;
  color: #4d5cdf;
  margin-top: 0.5rem;
  display: flex;
  gap: 3px;
}

.Work_hashtag {
  text-decoration: none;
  cursor: pointer;

  padding: 0; /* 패딩 제거 */
  font: inherit; /* 기본 글꼴 유지 */
  border: none; /* 테두리 제거 */
  background-color: transparent; /* 배경색 제거 */
  color: #feb47b;
  font-family: Pretendard;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.Work_buy-btn-info {
  border-radius: 26px;
  background: #feb47b;
  width: 98px;
  height: 30px;
  padding: 14px 16px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  color: #000;
  font-family: Pretendard;
  font-size: 13px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.Work_btns {
  width: 100%;
  padding: 10px;
  display: flex;
  align-items: center;

  justify-content: space-between;
}
.Work_btns-left {
  display: flex;
  gap: 12px;
}
.Work_btn {
  color: #fff;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 4px;
}

.Work_button-icon {
  color: red;
  font-size: 1.5rem;
  padding: 0px;
}

.Work_button-text {
  font-size: 0.8rem;
}
.Work_ppl {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
.Work_ppl-model_background {
  width: 100%;
  cursor: pointer;
}

.Work_ppl-model {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-top: 1px solid #707070;
  border-bottom: 1px solid #707070;
  border-collapse: collapse;
  padding: 10px;
}

.Work_ppl-first {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.Work_ppl-image {
  zoom: 0.346;
  border: 8px solid #fff;
  width: 130px;
  aspect-ratio: 1;
  overflow: hidden;
  border: 3px solid #fff;
  border-radius: 200px;
}
.Work_ppl-info {
  margin-left: 5px;
  display: flex;
  flex-direction: column;

  height: auto;
  align-items: flex-start;
}

.Work_ppl-nickname {
  display: flex;
  align-items: center;
  justify-content: space-between;

  height: auto;
  font-family: Pretendard;
  font-size: 12px;
  font-weight: 700;
  line-height: 14px;
  letter-spacing: 0em;
  text-align: left;
}

.Work_ppl-additional-information {
  font-size: 0.8rem;
  margin-top: 5px;
  color: #787878;
  height: auto;
}
.Follow {
  font-family: Pretendard;
  font-size: 12px;
  font-weight: 700;
  line-height: 14px;
  letter-spacing: 0em;
  text-align: right;
  color: #feb47b;
}
.Work_preview {
  width: 100%;
  padding-left: 4px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.Work_preview-item {
  width: 33%;
  aspect-ratio: 1.4;
  border-radius: 6px;
  position: relative;
  overflow: hidden;
}

.Work_img {
  width: 100%;
  height: 100%;
}
.Work_detail {
  padding: 10px;
  width: 100%;
  letter-spacing: 0.04rem;
  font-family: Pretendard;
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
  letter-spacing: 0em;
  text-align: left;
  word-break: normal;
}

.Work_modal-btm-con {
  margin-top: 10px;
  width: 100%;
  background: #1c1c1c;
  border-radius: 6px;
  overflow: hidden;
}

.Modal_image {
  max-width: 100%;
  max-height: 70vh; /* 이미지 크기 제한 */
  width: 440px;
}

.Modal button {
  margin-top: 10px;
}
