.Mypage_background {
  width: 100%;
  height: 100vh;
  flex-shrink: 0;
  border-radius: 24px;
  background: #222;

  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: flex-start;
  animation: Login_show-animation__kLAws 0.5s;

  color: #fff;
}

.Mypage_buttons {
  width: 940px;
  height: 55px;
  margin-top: 45px;
  display: flex;
  flex-direction: row;
  gap: 20px;
}
.Mypage_first_button {
  display: flex;
  width: 300px;
  padding: 18px 40px;
  justify-content: center;
  align-items: center;
  border-radius: 46px;
  border: 0px solid;
  background: inherit;
  cursor: pointer;
  color: #fff;
  background: #5b5b5b;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.Subscribe_button {
  display: flex;
  width: 174px;
  padding: 12px 40px;
  justify-content: center;
  background: transparent;
  align-items: center;
  gap: 8px;
  border-radius: 46px;
  border: 1px solid #757575;
  color: #fff;
  font-family: Pretendard;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

/*별 충전*/
.Star_background {
  width: 1228px;
  background: #222;
  height: 536px;
  display: flex;
  flex-direction: row;
  gap: 40px;
}

.Star_background-second {
  width: 594px;
  height: 538px;
  background: inherit;
  color: white;
}

.Star_background-third {
  height: 292px;
  margin-bottom: 24px;
  padding: 24px;
}

.Star_amount-buttons {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  height: 200px; /* 또는 적절한 높이 */
  row-gap: 10px;
  column-gap: 12px;
  margin-top: 20px;
}

.Star_amount-button {
  display: flex;
  width: 174px;
  padding: 18px 40px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  border-radius: 46px;
  border: 1px solid #757575;
  color: #fff;
  font-family: Pretendard;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}
.Star_background-forth {
  height: 204px;
  margin-bottom: 16px;
  color: #fff;
  font-family: Pretendard;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  padding: 24px 23px 24px 24px;
  gap: 20px;
}

.Star_amount_result-first {
  color: #fff;
  font-family: Pretendard;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.Star_amount_result-second {
  display: flex;
  justify-content: space-between;
}

.Star_amount_VAT {
  color: #fff;
  text-align: right;
  font-family: Pretendard;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  opacity: 0.4;
}

.Star_background-fifth {
  height: 536px;
  padding: 24px 24px 27px 24px;
  gap: 20px;
}

.Payment_buttons {
  height: 55px;

  display: flex;
  flex-direction: row;
  gap: 8px;
}
.Payment_button {
  display: flex;
  width: 174px;

  height: 43px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  border-radius: 46px;
  border: 1px solid #757575;
  color: #fff;
  font-family: Pretendard;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.Payment_textfields {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.Payment_container_row {
  display: flex;
  flex-direction: row;
  gap: 8px;
}
.Payment_container {
  margin-bottom: 20px;
}
.Payment_Card-number {
  display: flex;
  width: 546px;
  padding: 14px 18px;
  align-items: center;

  border-radius: 6px;
  background: #3c3c3c;
}

.Payment_Validity-date {
  display: flex;
  width: 85px;
  padding: 14px 18px;
  align-items: center;
  gap: 10px;
  border-radius: 6px;
  background: #3c3c3c;
  height: auto;
}

.Paymnent_card-password {
  display: flex;
  width: 85px;
  padding: 14px 18px;
  align-items: center;
  gap: 10px;
  border-radius: 6px;
  background: #3c3c3c;
}
.Payment_PayRing {
  display: flex;
  width: 546px;
  padding: 14px 18px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  border-radius: 6px;
  background: #feb47b;
  color: #000;
  font-family: Pretendard;
  font-size: 15px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.Payment_Paying-realtime {
  display: flex;
  width: 546px;
  padding: 14px 18px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  border-radius: 6px;
  background: #feb47b;
  color: #292929;
  font-family: Pretendard;
  font-size: 15px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.Star_background-third,
.Star_background-forth,
.Star_background-fifth {
  border-radius: 24px;
  background: #292929;
  flex-shrink: 0;
  width: 594px;
  display: flex;
  flex-direction: column;
}

.subscribe_models {
  margin-top: 40px;
  display: flex;
  gap: 40px;
}
.subscribe_model {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 120px;
  height: 162px;
  gap: 8px;
}

.subscribe_model-nametag {
  display: flex;
  flex-direction: column;
  align-items: center;
}
