.custom-arrow {
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff; /* 배경색 설정 */
  color: #000; /* 글자색 설정 */
  cursor: pointer;
  font-size: 18px;
  border-radius: 50%; /* 원형 모양으로 만들기 */
  border: 1px solid #ccc; /* 테두리 설정 */
}

.custom-prev-arrow {
  position: absolute;
  height: 100%;
  background: #000;
  opacity: 0.5;
  font-size: 2rem;
  color: #fff;
  padding: 5px;
  z-index: 1;
  left: 0;
}

.custom-next-arrow {
  right: -60px; /* 우측 화살표 위치 조정 */
}

.Slider_con {
  margin: 0px 55px;
}
