@media (min-width: 681px) {
  .Login_login-form {
    width: 668px;
    height: 361px;
    flex-shrink: 0;
    border-radius: 24px;
    background: #292929;
    position: relative;
    /*position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);*/
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    animation: Login_show-animation__kLAws 0.5s;

    padding: 40px;

    color: #fff;
  }

  mini-container {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
    background: transparent;
    text-align: left;
  }
  .Login_border-row {
    margin-left: 32px;
    margin-right: 32px;
    height: 280px;
  }
  .Login_textfields {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .container_input {
    margin-bottom: 8px;
  }
  .input_title {
    color: #fff;
    font-family: Pretendard;
    font-size: 13px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }

  .Login_textfield {
    display: flex;
    width: 252px;
    padding: 14px 18px;
    align-items: center;
    border-radius: 6px;
    background: #3c3c3c;
    height: 46px;
    color: #fff;
    font-family: Pretendard;
    font-size: 15px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-align: left;
    border-color: transparent;
  }

  .Login_buttons {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    margin-top: 12px;
  }

  .orange_btn,
  .grey_btn,
  .green_btn {
    display: flex;
    width: 252px;
    padding: 14px 18px;
    justify-content: center;
    align-items: center;
    border-radius: 6px;
    border-color: transparent;
    height: auto;
  }

  .green_btn {
    background: rgb(65, 99, 65);
    margin-bottom: 12px;
    color: #000;
    font-family: Pretendard;
    font-size: 15px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
  }
  .orange_btn {
    background: #feb47b;
    margin-bottom: 12px;
    color: #000;
    font-family: Pretendard;
    font-size: 15px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
  }

  .grey_btn {
    background: #5b5b5b;
    margin-bottom: 12px;

    font-family: Pretendard;
    font-size: 15px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
  }
  .register_text {
    cursor: pointer;
    text-decoration: none;
    color: #fff;
  }
  .Login_find-id-ps {
    font-family: Pretendard;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }

  .Login_right-section {
    margin-top: 32px;
  }
  .Login_img_language {
    flex-direction: row;
    justify-content: space-between;
    display: flex;
    width: 100%;
  }
  /*  로그인 내부  오른쪽 영역*/
  .only_adult {
    font-family: Pretendard;
    font-size: 24px;
    font-weight: 700;
    line-height: 29px;
    fill: #feb47b;
  }
  .Login_language {
    display: flex;
    width: 107px;
    height: 46px;

    justify-content: center;
    align-items: center;
    gap: 10px;
    border-radius: 6px;
    background: #3c3c3c;
    color: #fff;
    font-family: Pretendard;
    font-size: 15px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }
  .css-13cymwt-control {
    border-radius: 2px;
  }
  .Language-options {
    margin-top: 75px;
    position: relative;
    top: 100%; /* 토글 버튼 아래에 위치하도록 설정 */
    left: 0;
    background-color: transparent;
    border: 1px solid #ccc;
    padding: 5px;
    z-index: 1; /* 다른 요소 위에 레이어되도록 설정 */
    color: #000;
  }
  .Login_alert {
    color: #fff;
    font-family: Pretendard;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 16px; /* 133.333% */
    width: 272px;
    margin-top: 8px;
    word-break: normal;
  }
}
