@media (min-width: 681px) {
  .Register_register-form {
    width: 668px;
    min-height: 660px;
    flex-shrink: 0;
    border-radius: 24px;
    background: #292929;

    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    align-items: center;
    justify-content: flex-start;
    animation: Login_show-animation__kLAws 0.5s;

    padding: 40px;

    color: #fff;
  }
  .error {
    color: #ef6565;
    font-size: 13px;
    font-weight: 400;
  }
  .conditions_warning {
    color: #fff;
    font-family: Pretendard;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }
  .conditions {
    color: #fff;
    font-family: Pretendard;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-decoration-line: underline;
    margin-left: 3px;
  }
}
