@media (min-width: 681px) {
  .Find_find-form {
    width: 668px;
    min-height: 260px;
    flex-shrink: 0;
    border-radius: 24px;
    background: #292929;

    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    animation: Login_show-animation__kLAws 0.5s;

    padding: 40px;

    color: #fff;
    box-sizing: border-box;
  }

  .Find_language {
    flex-direction: row;
    justify-content: flex-end;
    display: flex;
    width: 100%;
    margin-bottom: 18px;
  }
}
