.slide_copy-vidoes {
  margin: 0px 20px 0px 40px;
}

.Post_position {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  /* margin: calc(70px + 3vh) 0;*/
  -webkit-overflow-scrolling: touch;
  overflow: auto;
  outline: none;
}

.copy_video_400 {
  height: 400px;
  width: 95%;

  border-radius: 8px;
  object-fit: cover;
}

.Post_overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  overflow-y: hidden;
  z-index: 10000;
}

.Post_modal-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 1200px;
  height: 100vh;
  box-shadow: 0px 4px 16px 0px rgba(0, 0, 0, 0.5);
  border-radius: 8px;
  background: #323232;
  overflow: hidden;
}

.Post_left-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 50%;
  height: 100%;
  position: relative;
}

.Video_main {
  width: 100%;
  height: 100%;
  margin: 0 auto;
}
.Post_right-content {
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.Post_info {
  width: 100%;
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.Post_border {
  width: 100%;
  border-bottom: 1px solid #707070;
}
.Post_reactions {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
