/* CSS 파일 (css_combine.css 또는 관련 파일) */
/* 모달 스타일 */
.blur-effect {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.7); /* 배경을 흐리게 만듭니다 */
  z-index: 1000; /* 다른 요소 위에 나타나도록 설정 */
}

/* 사이드바 스타일 */

.Sidebar_middle {
  margin-top: px;
}
.Sidebar_all-buttons {
  height: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}
.Sidebar_content {
  padding: 25px 20px 20px 21px;
  z-index: 1000;
  color: #fff;
  height: 100vh;
  width: 400px; /* 원하는 너비로 조정합니다 */

  background: #292929;
  position: fixed;
  left: 0;
  top: 0;
  /* overflow-y: auto; 내용이 넘치면 스크롤바 표시 */
  overflow: hidden;
  z-index: 1200; /* 모달 위에 나타나도록 설정 */
  transform: none;
  transition: transform 225ms cubic-bezier(0, 0, 0.2, 1) 0ms;
  color: #fff;
  display: flex;
  flex-direction: column;
}

.Sidebar_top {
  display: flex;

  justify-content: space-between;
  align-items: center;
}
.Profile_container {
  width: 100%;
  cursor: pointer;
}
.Profile_main {
  display: inline-flex;
  align-items: center;
}
.Profile_imamge {
  zoom: 0.307;
  border: 8px solid #fff;
}

.Profile_nickname {
  margin-left: 8px;

  color: #fff;

  font-size: 15px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.Profile_level {
  margin: 0.5rem 0 0 0.5rem;
}
.Profile_level-text {
  background-color: #a19e9d;
  color: #fff;
  margin-left: 1px;
  padding: 2px 5px;
  border-radius: 2px;
  background-color: #fff;
  color: #000;
  letter-spacing: 0.1rem;
  font-size: 0.8rem;
  text-shadow: -1px 0 #444, 0 1px #444, 1px 0 #444, 0 -1px #444;
}

.DM-button {
  flex-shrink: 0;
  cursor: pointer;

  display: flex;
  width: 174px;
  padding: 18px 40px;
  height: 55px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  color: #fff;
  font-family: Pretendard;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  border-radius: 46px;
  background: #5b5b5b;
}

.Sidebar_second {
  margin-top: 24px;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.Cash_container {
  display: flex;
  width: 174px;
  padding: 18px 40px;
  justify-content: center;
  align-items: center;
  height: 60px;
  gap: 8px;
  border-radius: 46px;
  border: 1px solid #757575;
}

.Sidebar_buttons {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.mypage_button,
.wishlist_button,
.purchases {
  display: flex;
  width: 359px;
  padding: 18px 40px;
  justify-content: center;
  align-items: center;
  border-radius: 46px;
  border: 1px solid #feb47b;
  background: inherit;
  cursor: pointer;
}

.wishlist_button {
  color: #feb47b;

  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}
.mypage_button {
  color: #feb47b;

  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.purchases {
  background: #feb47b;
  color: #000;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.Sidebar_bottom-buttons {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.Sidebar_bottom-button {
  display: flex;
  width: 359px;
  padding: 18px 40px;
  justify-content: center;
  align-items: center;
  border-radius: 46px;
  border: 1px solid #757575;
  color: #fff;
  height: 55px;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.Sidebar_notify-container {
  width: 400px;
  height: auto;
}
